.main-content {
  padding: 50px 30px 0 280px;
}
.main-content--hide {
  padding-left: 90px;
}

.main-collapser {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-bottom: 15px;
}

.card {
  border: 2px solid #000099;
  color: #000099;
}

.accordion {
  border: 2px solid #000099;
}
.accordion--hide .accordion__content {
  max-height: 0;
  border: none;
}
.accordion--hide .accordion__header__img {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.accordion__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px;
  background-color: #FFFFFF;
}
.accordion__header__img {
  width: 15px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.accordion__content {
  border-top: 1px solid #000099;
  border-bottom: 1px solid #000099;
  max-height: 1000px;
  overflow: hidden;
}
.accordion__content:last-child {
  border-bottom: none;
}
.accordion__content__item {
  padding: 10px;
}

.accordion-child--hide .accordion-child__content {
  max-height: 0;
  border: none;
}
.accordion-child--hide .accordion-child__header__img {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.accordion-child__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px;
  border-top: 1px solid #000099;
}
.accordion-child__header__img {
  width: 15px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.accordion-child__content {
  border-top: 1px solid #000099;
  border-bottom: 1px solid #000099;
  overflow: hidden;
}
.accordion-child__content:last-child {
  border-bottom: none;
}
.accordion-child__content__item {
  padding: 10px;
  max-height: 250px;
  overflow: auto;
}

.menu {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #000099;
  cursor: pointer;
  pointer-events: auto;
  z-index: 2;
}

.links-parent{
  padding: 10px 5px;
  border-top: 1px solid black;
}

.links-parent__link-title,
.links-parent__link-note,
.links-parent__time-stamp,
.links-parent__link,
.links-parent__tags{
  margin-bottom: 3px;
}
.links-parent__item{
  margin-bottom: 10px;
  border-bottom: 1px solid black;
}